import { overlaps } from './util.js'

class House {
    constructor(x, y, config) {
        this.x = x
        this.y = y
        this.c = config

        this.hbx = this.x
        this.hby = this.y
        this.hbw = this.c[22].width
        this.hbh = this.c[22].height
        
        this.miny = this.y + 270
        this.grabbed = false
        this.bashed = false
    }

    render(c) {
        // draw the house
        c.drawImage(this.c[22], this.x, this.y)

        // render the hitbox
        /*
        c.strokeStyle = 'red'
        c.beginPath()
        c.rect(this.hbx, this.hby, this.hbw, this.hbh)
        c.stroke()
        */
    }

    update() {
        this.hbx = this.x
        this.hby = this.y
    }

    bash(power) {
        this.y += Math.ceil(power * 0.1)
        if(this.y > this.miny) {
            this.y = this.miny
        }
    }
}

export default House